.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;

  &__col {
    position: relative;
    z-index: 1;
  }

  &__item {
    @include frame('rect', 75%);

    img {
      transition: 0.3s;
    }
  }

  &__overlay {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    background-color: rgba(#000, 0.2);
    transition: 0.3s;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50%;
      opacity: 0;
    }
  }

  &__col:hover &__item img {
    transform: scale(1.05);
  }

  &__col:hover &__overlay {
    background-color: rgba(#000, 0);
  }

  @media (max-width: 575px) {
    grid-template-columns: 1fr 1fr;
  }
}

.intro {
  padding: 70px 0;

  &__title {
    font-family: $playFair;
    font-size: 72px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 35px;
  }

  &__desc {
    opacity: 0.8;

    ul {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      margin: 35px -15px;
      padding-left: 0;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }

      li {
        flex: 0 0 50%;
        max-width: 50%;
        padding: 15px 15px 15px 65px;
        font-size: 18px;
        position: relative;

        &::before {
          content: '';
          display: block;
          width: 10px;
          height: 10px;
          background-color: $primary;
          border-radius: 50%;
          position: absolute;
          top: 24px;
          left: 15px;
        }
      }
    }
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 50px;
    }
  }

  @media (max-width: 767px) {
    padding: 50px 0;

    &__title {
      font-size: 28px;
      margin-bottom: 20px;
    }

    &__desc {
      ul {
        li {
          flex: 0 0 100%;
          max-width: 100%;
          font-size: 16px;
          padding: 10px 15px 10px 50px;

          &::before {
            top: 19px;
          }
        }
      }
    }
  }
}

.participation {
  background-color: $black;

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 663px;
    max-height: 100vh;
    position: relative;
    z-index: 1;
    overflow: hidden;
  }

  &__img {
    max-width: 70%;
    object-fit: contain;
    object-position: 0 0;
    position: absolute;
    z-index: -1;
    top: 10px;
    left: 0;
  }

  &__title {
    font-family: $playFair;
    font-size: 72px;
    text-align: right;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 30px;
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 45px;
      margin-bottom: 20px;
    }
  }

  @media (max-width: 575px) {
    &__inner {
      height: 40vh;
    }

    &__title {
      font-size: 26px;
      margin-bottom: 16px;
    }
  }
}

.partners {
  padding: 70px 0 35px;
  background-color: #3a3a3a;

  &__title,
  &__subtitle {
    font-size: 60px;
    font-family: $playFair;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 38px;
  }

  &__subtitle {
    font-size: 36px;
    margin-bottom: 18px;
    margin-top: 30px;
  }

  &__wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__logo,
  &__logo-2 {
    height: 70px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 15px 30px;

    img {
      max-height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
      filter: saturate(0);
      transition: 0.3s;
    }

    &:hover img {
      filter: saturate(1);
    }
  }

  &__logo-2 {
    height: 50px;
    margin: 15px;
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 40px;
      margin-bottom: 16px;
    }

    &__subtitle {
      font-size: 24px;
      margin-bottom: 16px;
    }
  }

  @media (max-width: 575px) {
    &__logo {
      height: 50px;
      margin: 12px 8px;
    }

    &__logo-2 {
      height: 40px;
      margin: 12px 8px;
    }
  }
}

.special {
  padding: 110px 0 50px;

  &__title {
    font-family: $playFair;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 40px;
  }

  &__wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__logo {
    height: 168px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 15px 40px 40px;

    img {
      max-height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
      filter: saturate(0);
      transition: 0.3s;
    }

    &:hover img {
      filter: saturate(1);
    }
  }

  @media (max-width: 767px) {
    padding: 60px 0 30px;

    &__title {
      font-size: 24px;
      margin-bottom: 16px;
    }

    &__logo {
      height: 80px;
      margin: 15px;
    }
  }

  @media (max-width: 767px) {
    &__logo {
      margin: 12px 8px;
    }
  }
}
