.header {
  position: relative;
  z-index: 100;

  &__wrapper {
    width: 100%;
    background-color: rgba(#000, 0.7);
    position: fixed;
    top: 0;
    left: 0;
  }

  &__inner {
    display: flex;
    align-items: center;
    padding: 8px 0;
  }

  &__logo {
    display: inline-flex;
    margin-right: auto;

    img {
      height: 90px;
    }
  }

  &__btn {
    color: $white;
  }
}
