.banner-slider {
  position: relative;
  z-index: 1;

  &__pagination {
    position: absolute;
    z-index: 10;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);

    .swiper-pagination-bullet {
      width: 16px;
      height: 16px;
      background-color: #fff;
      opacity: 1;
      margin: 0 6px;

      &:focus {
        outline: 0;
      }

      &-active {
        background-color: $primary;
      }
    }
  }

  .swiper-slide {
    background-color: $black;
  }

  @media (max-width: 991px) {
    &__pagination {
      bottom: 10px;

      .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
      }
    }
  }
}

.testimonial-slider {
  background-color: $black;
  padding: 125px 0;

  @media (max-width: 767px) {
    padding: 60px 0;
  }
}

.logo-slider {
  background-color: #3a3a3a;

  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }

  &__item {
    display: inline-flex;

    img {
      height: 60px;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }
}
