.banner {
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  padding-top: 106px;
  background-color: $black;
  overflow: hidden;
  position: relative;
  z-index: 1;

  &__img {
    height: 100%;
    width: 50%;
    object-fit: cover;
    object-position: 50% 50%;
    position: absolute;
    z-index: -2;
    top: 0;
    right: 0;
  }

  &__subtitle {
    font-size: 18px;
    text-transform: uppercase;
    color: $white;
  }

  &__title {
    font-family: $playFair;
    font-size: 72px;
    font-weight: 700;
    color: $white;
    margin: 0 0 20px;
  }

  &__time {
    display: inline-block;
    font-family: $playFair;
    font-size: 36px;
    font-weight: 700;
    padding: 0 24px;
    line-height: 1.95;
    border: 1px solid $white;
    color: $white;
    margin-bottom: 70px;
  }

  @media (max-width: 1199px) {
    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      background-color: rgba(#000, 0.3);
    }
  }

  @media (max-width: 1199px) and (orientation: portrait) {
    &__img {
      width: 100%;
    }
  }

  @media (max-width: 991px) {
    &__subtitle {
      font-size: 14px;
    }

    &__title {
      font-size: 30px;
    }

    &__time {
      font-size: 16px;
      margin-bottom: 20px;
    }
  }
}

.video {
  position: relative;

  &__frame {
    @include frame('rect', 50%);
  }

  &__overlay {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(#000, 0.7);
    padding-bottom: 10%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    color: $white;
    transition: 0.3s;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: rgba(#000, 0.5);
    }
  }

  &__title {
    font-family: $playFair;
    font-size: 48px;
    text-transform: uppercase;
    text-align: center;
    color: $white;
    margin-bottom: 28px;
  }

  &__icon {
    height: 95px;
  }
}

.testimonial {
  align-items: center;

  &__avatar {
    @include frame('circle');
    width: 150px;
    margin-right: 60px;
  }

  &__text {
    color: $white;
    font-size: 16px;
  }

  @media (max-width: 767px) {
    &__avatar {
      margin-right: 30px;
    }

    &__text {
      font-size: 14px;
    }
  }

  @media (max-width: 575px) {
    flex-direction: column;

    &__avatar {
      width: 120px;
      margin: 0 auto 20px;
    }

    &__text {
      text-align: center;
    }
  }
}

.button {
  display: inline-block;
  min-width: 175px;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 0 40px;
  line-height: 60px;
  background: $primary;
  color: $white;
  transition: 0.3s;
  border: 0;

  &:hover {
    color: $white;
    background-color: lighten(#000, 30%);
    text-decoration: none;
  }

  @media (max-width: 767px) {
    min-width: none;
    font-size: 14px;
    line-height: 40px;
    padding: 0 30px;
  }
}
